import { BrowserRouter, Route, Routes } from "react-router-dom";
import { SnackbarProvider } from "notistack";
import Home from "./Home/Home";
import Encoder from "Encoder/Encoder";

function App() {
  document.title = "My Steganography manager";
  const currentLocation = window.location.href;
  const basename = currentLocation.includes("index.html") ? "/index.html" : "/";

  return (
    <div
      className={`stega-wrapper${
        currentLocation.includes("index.html") ? " popup" : ""
      }`}
    >
      <SnackbarProvider maxSnack={4} autoHideDuration={3000}>
        <BrowserRouter basename={basename}>
          <Routes>
            <Route path="/" element={<Home />} />
            <Route path="/home" element={<Home />} />
            <Route path="/encode" element={<Encoder />} />
            {/* ERROR */}
            <Route
              path="*"
              element={
                <div id="fullscreen">
                  <span>Your princess is in another castle!</span>
                </div>
              }
            />
          </Routes>
        </BrowserRouter>
      </SnackbarProvider>
    </div>
  );
}

export default App;
